<template>
  <div class="animated fadeIn">
    <b-row class="justify-content-center">
      <b-col md="5">
        <b-card-group>
          <b-card no-body class="p-4">
            <img class="infoImageCenter" :src="'http://' + data.link_foto"></img>
            <div v-if="jukirStatus===jukir_status.status1">
              <div class="alert-box" style="background-color:red;">
                {{jukir_status_info.status1}}
              </div>
            </div>
            <div v-else-if="jukirStatus===jukir_status.status2">
              <div class="alert-box" style="background-color:green;">
                {{jukir_status_info.status2}}
              </div>
            </div>
            <div v-else>
              <div class="alert-box" style="background-color:orange;">
                {{jukir_status_info.status3}}
              </div>
            </div>

            <table class="tableJukirInfo">
              <tbody>
              <tr>
                <td>{{hdr_column.col1}}</td>
                <td>:</td>
                <td>{{data.ktalm}}</td>
              </tr>
              <tr>
                <td>{{hdr_column.col2}}</td>
                <td>:</td>
                <td>{{data.nm}}</td>
              </tr>
              <tr>
                <td>{{hdr_column.col3}}</td>
                <td>:</td>
                <td>{{data.lks}}</td>
              </tr>
              <tr>
                <td>{{hdr_column.col4}}</td>
                <td>:</td>
                <td>{{data.jalan}}</td>
              </tr>
              <tr>
                <td>{{hdr_column.col5}}</td>
                <td>:</td>
                <td>{{data.sdt}}</td>
              </tr>
              <tr>
                <td>{{hdr_column.col6}}</td>
                <td>:</td>
                <td>{{data.id}}</td>
              </tr>
              </tbody>
            </table>
          </b-card>
        </b-card-group>
      </b-col>
      <!--<p class="error-msg">{{ errorMsg }}</p>-->
    </b-row>
  </div>
</template>

<script>
  import {EventBus} from '../../_auth/utils/eventbus'

  export default {
    name: 'info-jukir',

    data() {
      return {
        jukir_status:{status1:0, status2:1, status3:-1},
        jukir_status_info:{status1:"BERAKHIR", status2:"BERLAKU", status3:"JUKIR TIDAK DITEMUKAN"},
        hdr_column: {
          col1: "No. KTA", col2: "Nama", col3: "Titik Parkir", col4: "Jalan",
          col5: "Berlaku s/d", col6: "ID"
        },
        data: {ktalm: "", nm: "", lks: "", jalan: "", sdt: "", id: "", status: -1, link_foto:""},
        jukirStatus: -1,
        errorMsg: ''
      }
    },

    beforeMount: function () {
      this.getData();
    },

    methods: {
      getData() {
        let str_id = this.$route.query.id;
        let n = str_id.indexOf("#");
        if(n==-1)
        {
          this.$store.dispatch('find_info_jukir_by_id', {id: str_id})
            .then(() => this.$router.push({
              path: 'info-jukir',
              params: {id: str_id},
              query: {id: str_id}
            }))
        }
        else {
          this.$store.dispatch('find_info_jukir_by_full_id', {full_id: str_id})
            .then(() => this.$router.push({
              path: 'info-jukir',
              params: {full_id: str_id},
              query: {id: str_id}
            }))
        }
      },
    },

    mounted() {
      EventBus.$on('success', (msg) => {
        this.data = msg[0];
        this.jukirStatus = this.data.status;
        this.errorMsg = "";
      });
      EventBus.$on('fail', (msg) => {
        this.data = {};
        this.jukirStatus = -1;
        this.link_foto = "";
        this.errorMsg = msg;
      })
    },

    beforeDestroy() {
      EventBus.$off('success');
      EventBus.$off('fail');
    },
  };
</script>


<style>
  @import "../../assets/scss/jukir_info_style.scss";
</style>
